exports.push([module.id, "\n.vue-modal label {\n    font-size: 14px;\n}\n.providerForm {\n    padding: 10px;\n}\n.validation-error {\n    padding: 3px;\n    margin-bottom: 10px;\n    border: 1px solid transparent;\n    border-radius: 4px;\n}\n.has-danger .form-control {\n    border-color: #ff0000;\n    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);\n}\n.has-errors {\n    color: #a94442;\n}\n", "", {"version":3,"sources":["/Users/michalis/Code/CLH/cpm-api/resources/assets/js/components/pages/view-care-plan/update-care-person.vue?2aecebcc"],"names":[],"mappings":";AACA;IACA,gBAAA;CACA;AAEA;IACA,cAAA;CACA;AAEA;IACA,aAAA;IACA,oBAAA;IACA,8BAAA;IACA,mBAAA;CACA;AAEA;IACA,sBAAA;IACA,iDAAA;CACA;AAEA;IACA,eAAA;CACA","file":"update-care-person.vue","sourcesContent":["<style>\n    .vue-modal label {\n        font-size: 14px;\n    }\n\n    .providerForm {\n        padding: 10px;\n    }\n\n    .validation-error {\n        padding: 3px;\n        margin-bottom: 10px;\n        border: 1px solid transparent;\n        border-radius: 4px;\n    }\n\n    .has-danger .form-control {\n        border-color: #ff0000;\n        box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);\n    }\n\n    .has-errors {\n        color: #a94442;\n    }\n</style>\n\n<template>\n    <div>\n        <modal v-show=\"show\">\n            <template slot=\"header\">\n                <button type=\"button\" class=\"close\" @click=\"clearOpenModal\">×</button>\n                <h4 class=\"modal-title\">Provider Details</h4>\n            </template>\n\n            <template slot=\"body\">\n                <div v-if=\"validationErrors\" class=\"row providerForm\">\n                    <div class=\"error-list\">\n                        <h5 class=\"has-errors\">\n                            <u>There were some problems with your input. Please review the form.</u>\n                        </h5>\n                    </div>\n                </div>\n\n                <vue-form :state=\"formstate\" @submit.prevent=\"onSubmit\">\n                    <div class=\"row providerForm\">\n\n                        <div class=\"form-group\">\n                            <!--name-->\n                            <label class=\"col-md-3 control-label\">Provider Name</label>\n\n                            <div class=\"col-md-9\">\n                                <div class=\"row\">\n                                    <div class=\"form-group required-field col-md-6\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.first_name)\">\n                                            <div class=\"col-md-12\">\n                                                <input type=\"text\"\n                                                       id=\"first_name\"\n                                                       name=\"first_name\"\n                                                       class=\"form-control input-md\"\n                                                       placeholder=\"First\"\n                                                       required\n                                                       v-model=\"formData.user.first_name\">\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"first_name\"\n                                                                show=\"$untouched || $touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"required\">\n                                                        *required\n                                                    </div>\n                                                </field-messages>\n                                            </div>\n                                        </validate>\n                                    </div>\n\n                                    <div class=\"form-group required-field col-md-6\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.last_name)\">\n                                            <div class=\"col-md-12\">\n                                                <input type=\"text\"\n                                                       id=\"last_name\"\n                                                       name=\"last_name\"\n                                                       class=\"form-control input-md\"\n                                                       placeholder=\"Last\"\n                                                       required\n                                                       v-model=\"formData.user.last_name\">\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"last_name\"\n                                                                show=\"$untouched || $touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"required\">\n                                                        *required\n                                                    </div>\n                                                </field-messages>\n                                            </div>\n                                        </validate>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n\n                    <!--specialty-->\n                    <div class=\"row providerForm\">\n                        <div class=\"form-group\">\n                            <label class=\"col-md-3 control-label\">Specialty</label>\n                            <div class=\"col-md-9\">\n                                <div class=\"row\">\n                                    <div class=\"col-md-12\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.specialty)\">\n                                            <div class=\"col-md-12\">\n                                                <select2 :options=\"specialtiesOptions\"\n                                                         name=\"specialty\"\n                                                         v-model=\"formData.user.provider_info.specialty\"\n                                                         style=\"width: 100%;\">\n                                                    <option disabled value=\"0\">Select one</option>\n                                                </select2>\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"specialty\" show=\"$touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"required\">\n                                                        *required\n                                                    </div>\n                                                </field-messages>\n                                            </div>\n\n                                        </validate>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n\n                    <!--address-->\n                    <div class=\"row providerForm\">\n                        <div class=\"form-group\">\n                            <label class=\"col-md-3 control-label\">Address</label>\n                            <div class=\"col-md-9\">\n                                <div class=\"row\">\n                                    <div class=\"col-md-8\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.address)\">\n                                            <div class=\"col-md-12\">\n                                                <input type=\"text\"\n                                                       id=\"address\"\n                                                       name=\"address\"\n                                                       class=\"form-control input-md\"\n                                                       placeholder=\"Line 1\"\n                                                       v-model=\"formData.user.address\">\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"address\" show=\"$touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"required\">\n                                                        *required\n                                                    </div>\n                                                </field-messages>\n                                            </div>\n\n                                        </validate>\n                                    </div>\n\n                                    <div class=\"col-md-4\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.address_2)\">\n                                            <div class=\"col-md-12\">\n                                                <input type=\"text\"\n                                                       id=\"address_2\"\n                                                       name=\"address_2\"\n                                                       class=\"form-control input-md\"\n                                                       placeholder=\"Line 2\"\n                                                       v-model=\"formData.user.address2\">\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"address_2\" show=\"$touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\"\n                                                         slot=\"required\"></div>\n                                                </field-messages>\n                                            </div>\n\n                                        </validate>\n                                    </div>\n                                </div>\n                                <div class=\"row\">\n                                    <div class=\"col-md-4\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.city)\">\n                                            <div class=\"col-md-12\">\n                                                <input type=\"text\"\n                                                       id=\"city\"\n                                                       name=\"city\"\n                                                       class=\"form-control input-md\"\n                                                       placeholder=\"City\"\n                                                       v-model=\"formData.user.city\">\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"city\" show=\"$touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"required\">\n                                                        *required\n                                                    </div>\n                                                </field-messages>\n                                            </div>\n\n                                        </validate>\n                                    </div>\n\n                                    <div class=\"col-md-4\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.state)\">\n                                            <div class=\"col-md-12\">\n                                                <input type=\"text\"\n                                                       id=\"state\"\n                                                       name=\"state\"\n                                                       class=\"form-control input-md\"\n                                                       placeholder=\"State\"\n                                                       v-model=\"formData.user.state\">\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"state\" show=\"$touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"required\">\n                                                        *required\n                                                    </div>\n                                                </field-messages>\n                                            </div>\n\n                                        </validate>\n                                    </div>\n\n                                    <div class=\"col-md-4\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.zip)\">\n                                            <div class=\"col-md-12\">\n                                                <input type=\"text\"\n                                                       id=\"zip\"\n                                                       name=\"zip\"\n                                                       class=\"form-control input-md\"\n                                                       placeholder=\"Zip\"\n                                                       v-model=\"formData.user.zip\">\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"zip\" show=\"$touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"required\">\n                                                        *required\n                                                    </div>\n                                                </field-messages>\n                                            </div>\n\n                                        </validate>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n\n                    <!--phone-->\n                    <div class=\"row providerForm\">\n                        <div class=\"form-group\">\n\n                            <label class=\"col-md-3 control-label\">Phone Number</label>\n\n                            <div class=\"col-md-9\">\n                                <div class=\"row\">\n                                    <div class=\"form-group required-field col-md-12\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.phone)\">\n                                            <div class=\"col-md-12\">\n                                                <input type=\"text\"\n                                                       id=\"phone\"\n                                                       name=\"phone\"\n                                                       class=\"form-control input-md\"\n                                                       placeholder=\"xxx-xxx-xxxx\"\n                                                       v-model=\"formData.user.phone_numbers[0].number\">\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"phone\" show=\"$touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"required\">\n                                                        *required\n                                                    </div>\n                                                </field-messages>\n                                            </div>\n                                        </validate>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n\n                    <!--practice-->\n                    <div class=\"row providerForm\">\n                        <div class=\"form-group\">\n\n                            <label class=\"col-md-3 control-label\">Practice Name</label>\n\n                            <div class=\"col-md-9\">\n                                <div class=\"row\">\n                                    <div class=\"form-group required-field col-md-12\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.practice)\">\n                                            <div class=\"col-md-12\">\n                                                <input type=\"text\"\n                                                       id=\"practice\"\n                                                       name=\"practice\"\n                                                       class=\"form-control input-md\"\n                                                       placeholder=\"\"\n                                                       v-model=\"formData.user.primary_practice.display_name\">\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"practice\" show=\"$touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"required\">\n                                                        *required\n                                                    </div>\n                                                </field-messages>\n                                            </div>\n                                        </validate>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n\n\n                    <!--email-->\n                    <div class=\"row providerForm\">\n                        <div class=\"form-group\">\n\n                            <label class=\"col-md-3 control-label\">Email</label>\n\n                            <div class=\"col-md-9\">\n                                <div class=\"row\">\n                                    <div class=\"form-group required-field col-md-6\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.email)\">\n                                            <div class=\"col-md-12\">\n                                                <input type=\"email\"\n                                                       id=\"email\"\n                                                       name=\"email\"\n                                                       class=\"form-control input-md\"\n                                                       v-model=\"formData.user.email\">\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"email\" show=\"$touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"required\">\n                                                        *required\n                                                    </div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"email\">\n                                                        Please enter a valid email\n                                                    </div>\n                                                </field-messages>\n                                            </div>\n                                        </validate>\n                                    </div>\n\n                                    <!--send alerts-->\n                                    <div class=\"form-group col-md-6\">\n\n                                        <label class=\"col-md-3 control-label\">Send Alerts</label>\n\n                                        <div class=\"col-md-9\">\n                                            <div class=\"row\">\n                                                <div class=\"form-group required-field col-md-12\">\n                                                    <validate auto-label :class=\"fieldClassName(formstate.send_alerts)\">\n                                                        <div class=\"col-md-12\">\n\n                                                            <input v-model=\"formData.alert\"\n                                                                   id=\"send_alerts\"\n                                                                   name=\"send_alerts\"\n                                                                   class=\"form-control input-md\"\n                                                                   type=\"checkbox\"\n                                                                   v-bind:disabled=\"!formData.user.email || formstate.email && !formstate.email.$valid\"\n                                                                   style=\"display: inline;\">\n                                                        </div>\n\n                                                        <div class=\"col-md-12\">\n                                                            <field-messages name=\"send_alerts\"\n                                                                            show=\"$touched || $submitted\">\n                                                                <div></div>\n                                                                <div class=\"validation-error has-errors text-right\"\n                                                                     slot=\"required\">\n                                                                    *required\n                                                                </div>\n                                                            </field-messages>\n                                                            <div v-if=\"!formData.user.email || formstate.email && !formstate.email.$valid\"\n                                                                 class=\"validation-error text-left\"\n                                                                 style=\"color: green;\">\n                                                                Email needs to be filled out and valid.\n                                                            </div>\n                                                        </div>\n                                                    </validate>\n                                                </div>\n                                            </div>\n                                        </div>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n\n                    <div class=\"row providerForm\">\n                        <div class=\"form-group\">\n\n                            <label class=\"col-md-3 control-label\">Clinical Type</label>\n\n                            <div class=\"col-md-9\">\n                                <div class=\"row\">\n                                    <!--clinical type-->\n                                    <div class=\"form-group required-field col-md-6\">\n                                        <validate auto-label :class=\"fieldClassName(formstate.qualification)\">\n                                            <div class=\"col-md-12\">\n\n                                                <select v-model=\"formData.user.provider_info.qualification\"\n                                                        id=\"qualification\"\n                                                        name=\"qualification\"\n                                                        class=\"form-control input-md\"\n                                                        required>\n                                                    <option value=\"\" disabled></option>\n                                                    <option value=\"clinical\">Clinical (MD, RN or other)</option>\n                                                    <option value=\"non-clinical\">Non-clinical</option>\n                                                </select>\n                                            </div>\n\n                                            <div class=\"col-md-12\">\n                                                <field-messages name=\"qualification\"\n                                                                show=\"$untouched || $touched || $submitted\">\n                                                    <div></div>\n                                                    <div class=\"validation-error has-errors text-right\" slot=\"required\">\n                                                        *required\n                                                    </div>\n                                                </field-messages>\n                                            </div>\n                                        </validate>\n                                    </div>\n\n                                    <!--is ccm billing provider-->\n                                    <div class=\"form-group col-md-6\">\n\n                                        <label class=\"col-md-3 control-label\">CCM Billing Provider</label>\n\n                                        <div class=\"col-md-9\">\n                                            <div class=\"row\">\n                                                <div class=\"form-group required-field col-md-12\">\n                                                    <validate auto-label\n                                                              :class=\"fieldClassName(formstate.is_billing_provider)\">\n                                                        <div class=\"col-md-12\">\n\n                                                            <input v-model=\"formData.is_billing_provider\"\n                                                                   id=\"is_billing_provider\"\n                                                                   name=\"is_billing_provider\"\n                                                                   class=\"form-control input-md\"\n                                                                   type=\"checkbox\"\n                                                                   style=\"display: inline;\">\n                                                        </div>\n\n                                                        <div class=\"col-md-12\">\n                                                            <field-messages name=\"is_billing_provider\"\n                                                                            show=\"$touched || $submitted\">\n                                                                <div></div>\n                                                                <div class=\"validation-error has-errors text-right\"\n                                                                     slot=\"required\">\n                                                                    *required\n                                                                </div>\n                                                            </field-messages>\n                                                        </div>\n                                                    </validate>\n                                                </div>\n                                            </div>\n                                        </div>\n                                    </div>\n                                </div>\n                            </div>\n                        </div>\n                    </div>\n                </vue-form>\n            </template>\n\n            <template slot=\"footer\">\n                <div class=\"row\">\n                    <div class=\"col-md-6 text-center\">\n                        <button style=\"width:50%\" class=\"btn btn-default\" @click=\"clearOpenModal\">Close</button>\n                    </div>\n                    <div class=\"col-md-6 text-center\">\n                        <button style=\"width:50%\" :disabled=\"false\" @click=\"sendForm\" class=\" btn btn-info\">\n                            Save <i v-if=\"sending\" class=\"fa fa-spinner fa-pulse fa-fw\"></i>\n                        </button>\n                    </div>\n\n                </div>\n            </template>\n        </modal>\n    </div>\n</template>\n\n<script>\n    import modal from '../../shared/modal.vue';\n    import {mapGetters, mapActions} from 'vuex'\n    import {getPatientCareTeam, clearOpenModal, addNotification, updateCarePerson} from '../../../store/actions'\n\n    export default {\n        props: {\n            show: {\n                type: Boolean,\n                default: false\n            },\n            carePerson: Object\n        },\n\n        components: {\n            modal\n        },\n\n        computed: Object.assign({},\n            {\n                validationErrors() {\n                    return this.formstate && this.formstate.$invalid && this.formstate.$touched && this.submitClicked\n                },\n            },\n            {\n                name() {\n                    return this.carePerson.user.first_name\n                        + ' '\n                        + this.carePerson.user.last_name\n                }\n            }\n        ),\n\n        methods: Object.assign(\n            mapActions(['getPatientCareTeam', 'clearOpenModal', 'addNotification', 'updateCarePerson']),\n            {\n                sendForm() {\n                    this.submitClicked = true\n\n                    this.sending = true\n\n                    if (this.validationErrors) {\n                        return\n                    }\n\n                    if (this.formData.is_billing_provider) {\n                        this.formData.formatted_type = 'Billing Provider';\n                    }\n\n                    let id = this.formData.id ? this.formData.id : 'new'\n\n                    this.updateCarePerson(this.formData)\n\n                    this.getPatientCareTeam(this.patientId)\n\n                    this.clearOpenModal();\n\n                    this.addNotification({\n                        title: \"Successfully saved Care Person\",\n                        text: \"\",\n                        type: \"success\",\n                        timeout: true\n                    })\n\n                    let url = window.location.href\n\n                    if(url.includes('view-careplan')) {\n                        window.location.replace(url)\n                    }\n                },\n\n                fieldClassName(field) {\n                    if (!field) {\n                        return '';\n                    }\n                    if ((field.$touched || field.$submitted) && field.$valid) {\n                        return 'has-success';\n                    }\n                    if ((field.$touched || field.$submitted) && field.$invalid) {\n                        return 'has-danger';\n                    }\n                },\n            }\n        ),\n\n        created() {\n            this.formData  = JSON.parse(JSON.stringify(this.carePerson))\n        },\n\n        data()\n        {\n            return {\n                submitClicked : false,\n                updateRoute: $('meta[name=\"provider-update-route\"]').attr('content'),\n                patientId: $('meta[name=\"patient_id\"]').attr('content'),\n                formstate: {},\n                specialtiesOptions: [\n                    {id: \"Abdominal Radiology\", text: \"Abdominal Radiology\"},\n                    {id: \"Addiction Psychiatry\", text: \"Addiction Psychiatry\"},\n                    {id: \"Adolescent Medicine\", text: \"Adolescent Medicine\"},\n                    {id: \"Adult Reconstructive Orthopaedics\", text: \"Adult Reconstructive Orthopaedics\"},\n                    {\n                        id: \"Advanced Heart Failure & Transplant Cardiology\",\n                        text: \"Advanced Heart Failure & Transplant Cardiology\"\n                    },\n                    {id: \"Allergy & Immunology\", text: \"Allergy & Immunology\"},\n                    {id: \"Anesthesiology\", text: \"Anesthesiology\"},\n                    {id: \"Biochemical Genetics\", text: \"Biochemical Genetics\"},\n                    {id: \"Blood Banking - Transfusion Medicine\", text: \"Blood Banking -T ransfusion Medicine\"},\n                    {id: \"Cardiology\", text: \"Cardiology\"},\n                    {id: \"Cardiothoracic Radiology\", text: \"Cardiothoracic Radiology\"},\n                    {id: \"Cardiovascular Disease\", text: \"Cardiovascular Disease\"},\n                    {id: \"Chemical Pathology\", text: \"Chemical Pathology\"},\n                    {id: \"Child & Adolescent Psychiatry\", text: \"Child & Adolescent Psychiatry\"},\n                    {id: \"Child Abuse Pediatrics\", text: \"Child Abuse Pediatrics\"},\n                    {id: \"Child Neurology\", text: \"Child Neurology\"},\n                    {id: \"Clinical & Laboratory Immunology\", text: \"Clinical & Laboratory Immunology\"},\n                    {id: \"Clinical Cardiac Electrophysiology\", text: \"Clinical Cardiac Electrophysiology\"},\n                    {id: \"Clinical Neurophysiology\", text: \"Clinical Neurophysiology\"},\n                    {id: \"Colon & Rectal Surgery\", text: \"Colon & Rectal Surgery\"},\n                    {id: \"Congenital Cardiac Surgery\", text: \"Congenital Cardiac Surgery\"},\n                    {id: \"Craniof